.webinar-videos {
  min-height: 891px;
  background: url(../../assets/img/background/home/3.jpg);
  background-repeat: no-repeat;
  width: 100%;
  padding-top: 100px;
  margin-bottom: 140px;
  position: relative;

  @media (max-width: 1250px) {
    min-height: 0;
    background: none;
  }
}

.webinar-videos__title {
  margin-bottom: 50px;

  @media (max-width: 1250px) {
    font-size: 19px;
    margin-bottom: 25px;
  }
}

.webinar-videos__box {
  position: relative;
  background: #FFFFFF;
  box-shadow: 0px -10px 25px rgba(0, 0, 0, 0.15);
  border-radius: 80px;
  padding: 60px;
  z-index: 10;

  @media (max-width: 1250px) {
    padding: 25px;
    border-radius: 20px;
  }
}

.webinar-videos__box__about {
  text-transform: uppercase;
  font-size: 24px;
  line-height: 144%;

  @media (max-width: 1250px) {
    font-size: 13px;
    line-height: 144%;
  }
}

.webinar-videos__subscribe-btn {
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
}